body, html {
	--bg: #f2f2f2;
	margin-top: 0;
	background-color: grey;
}

@media print {
	html, body {
		background-color: #293f50;
	}
}

.App {
	background-color: var(--bg);
	margin: auto;
	width: 21cm;
	min-height: calc(100vh - 4cm);
}

.exp-grid::after {
	content: '';
	background: url('./stencil.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	opacity: 0.5;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	position: absolute;
	z-index: 1;
}

.studies {
	background: linear-gradient(to right bottom, #648880, #293f50);
	color: #f0efd0;
	padding-top: 1cm;
	padding-bottom: 1cm;
}

.header {
	background: linear-gradient(to right bottom, #648880, #293f50);
	/* background-image: url('./banner.jpg'); */
	height: 4.5cm;
	position: relative;
}

.header::after {
	content: '';
	background: url('./banner.jpg');
	opacity: 0.08;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	position: absolute;
	z-index: 1;
}

.detail-grid {
	display: grid;
	padding-top: 0.5cm;
	padding-left: 1cm;
	padding-right: 1cm;
	color: #f0efd0;
	grid-template-columns: [start] 3em 1fr 3em 1fr 3em 1fr [end];
}

.double-height {
	grid-row: span 2;
}

.exp-grid {
	display: grid;
	grid-template-columns: [start] 1fr 1fr [end];
	padding: 1cm;
	padding-bottom: 1.5cm;
	grid-gap: 1cm;
	position: relative;
}

.exp-grid h2 {
	font-size: 1.1em;
}

.card {
	padding: 0.5cm;
	/* border: 1px solid rgba(0, 0, 0, 0.2); */
	border-radius: 4px;
	/*
	box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2),
		5px 5px 5px rgba(0, 0, 0, 0.05)
	;
	*/
	box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.05);
	text-align: justify;
	z-index: 2;
	background-color: var(--bg);
}

.grid {
	padding-left: 1cm;
	padding-right: 1cm;
	display: grid;
	grid-template-columns: [start] 7em 3fr [middle] 7em 3fr [end];
	grid-gap: 0.3cm;
	/* page-break-inside: avoid; */
	page-break-before: auto;
}

.title {
	color: #648880;
	border-bottom: 1px solid #648880;
}

.full-width {
	grid-column: start / end;
}

p:first-child {
	margin-top: 0;
}

.h-pad {
	height: 1cm;
}

.label {
	text-align: right;
	padding-right: 0.5cm;
	color: #abaa95;
}

.study-location {
	color: #abaa95;
	display: block;
}

.study-location:before {
	content: '(';
}

.study-location:after {
	content: ')';
}
